.card {
  background: #fff;
  border-radius: 2px;
  height: 9rem;
  margin: 1rem;
  position: relative;
  width: 9rem;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
  background: #55231c;
}

.card > .img-container {
  height: 8.8rem;
  overflow: hidden;
}
.card > .img-container > img {
  width: 100%;
}

.card:hover,
.card.click-item {
  cursor: pointer;
}

.card.grow {
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.grow:hover {
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
}



